import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http';
import { UtilsProvider } from '../../providers/utils/utils';
import { Storage } from '@ionic/storage';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the ChequePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-cheque',
  templateUrl: 'cheque.html',
})
export class ChequePage {

  public listaCheck : string[];
  public check : Array<string>;
  public message: string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public http: HttpProvider, public utils: UtilsProvider, private storage: Storage,
             public command: CommandsProvider) {
    
  }

  ionViewDidLoad() {

  }

  ionViewDidEnter(){      
    this.loadChecks();
  }

  //Metodo del cheque seleccionado
  selectedCheck(check){
     this.navCtrl.push("FiltroPage", { flagFiltro: 1 }); 
     //cheque seleccionado
     this.storage.set('checkSelected',check); 
  }

  private loadChecks() { 
    
    //var cmd = "E2LSCR12A";
    var command :string[] =  this.utils.getCommands(this.command.E2LSCR12A);
    
    var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW=%BA'+ 
    this.utils.separator +'%BA'+ this.utils.separator +'0%BA'+ this.utils.separator +'%BA'+this.utils.separator;

    this.http.callServer(URL).then(res => {      
    this.message = res.message;

      if (this.message != '' && typeof this.message!=="undefined") {
        this.utils.alertServidor(this.message);
      } else {
        //Lista con los numeros de cheques
        this.listaCheck = res.lista1;    
        
      }
    },
      error => {
        console.log(error);
      });
  }
  
  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }

}



